import React from 'react'

// Styles
import './About.scss'

const About = React.createClass({
  showMore: function (e) {
    e.preventDefault();
    console.log('button was clicked');
  },
  render () {
    return (
      <section className='section-item about-component'>
        <h3 className='section-title'>About</h3>
        <div className='section-content'>
          <p>I'm Nick, a privacy lawyer and software development consultant from Toronto. I advise organizations that are creating new, disruptive products in conditions of uncertainty on matters related to product design, development and data law (privacy, access to information, data security and information management).</p>
        </div>
      </section>
    )
  }
})

export default About



